@import url("https://fonts.googleapis.com/css2?family=Roboto+Flex:opsz,wght@8..144,200;8..144,400;8..144,600;8..144,900;8..144,1000&display=swap");
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #000;
  /* background: url(../../frontend/public/assets/fondo.jpg); */
  background-size: cover;
  background-repeat: repeat;
  color: #fff;
  font-family: "Roboto Flex";
}
a {
  text-decoration: none;
  color: #eee;
  display: flex;
  flex-flow: column;
}
a:hover {
  color: #d5cece;
}
code {
  font-family: "Noto Sans Mono", monospace;
}
li {
  list-style: none;
}
ul {
  display: flex;
  padding: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 1.2em;
}
img,
video {
  width: 100%;
  height: 100%;
}
input,
textarea,
button {
  border: none;
  outline: none;
}
div {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.link {
  text-decoration: none;
  color: #fff;
}

.header {
  background: transparent;
  padding: 1em 2em;

  display: flex;
  align-items: center;
  gap: 0.5em;
  justify-content: space-between;
  min-height: 7.5em;
  position: relative;
  z-index: 10;
}
.header__left {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 3em;
}
.header__left-img {
  width: 12em;
  cursor: pointer;
}
.header__left-div {
  /* background: green; */
}
.header__left-ul {
  font-size: 1.2em;
  display: flex;
  gap: 0.7em;
}
.header__left-li {
  padding: 0.5em;
  font-weight: bold;
  /* background: yellow; */
  transition: 0.5s ease;
  cursor: pointer;
}
.header__left-li:hover {
  color: #dcdbdb;
}
.header__left-li:nth-of-type(1) {
  transition: 0.5s linear;
}
.header__left-li:nth-of-type(1):hover {
  border: 0.1em solid #eee;
  border-radius: 0.5em;
  background: #eeeeee50;
}

.header__right {
  flex-grow: 1;
  display: flex;
  justify-content: end;
  align-items: center;
  gap: 0.7em;
}
.header__right-ul {
  font-size: 0.8em;
  display: flex;
  flex-flow: column;
  gap: 0.3em;
}
.header__right-li {
  display: flex;
  align-items: center;
  gap: 0.5em;
  justify-content: end;
}
.header__right-li-img {
  width: 1em;
}
.header__right-div {
  display: flex;
  align-items: center;
  padding: 1em;
  border: 0.15em solid #eee;
  border-radius: 1em;
  cursor: pointer;
  transition: 0.2s ease;
}
.header__right-div:hover {
  border: 0.15em solid #e66a11;
  transform: translateY(-0.5em);
}
.header__right-div-img {
  width: 2em;
}
.header__right-span {
}
.home-heros {
  display: flex;
  justify-content: center;
  align-items: center;
}
.home__container-presentacion {
