}
.home__presentacion-span {
  font-size: 2em;
}
.home__presentacion-div-div {
  display: flex;
  gap: 0.5em;
  align-items: center;
  justify-content: center;
  border: 0.2em solid #fff;
  border-radius: 0.5em;
