}
.home__noticias {
  position: absolute;
  bottom: 0;
  z-index: 10;
  right: 0;
  left: 0;
  margin: 0 0 2em 0;
}
.home__noticias-container {
  display: flex;
  justify-content: center;
  gap: 2em;
  margin: 0;
}
.home__noticias-div {
