}
.home__noticias-p {
  font-weight: bold;
  font-size: 1.3em;
  margin: 0;
}
.home__noticias-p-absolute {
  transform: translateY(6em);
  transition: 0.5s ease;
}
.home__noticias-div:hover .home__noticias-p-absolute {
  transform: translateY(0);
}
.home__noticias-div:hover .home__noticias-div-absolute {
  bottom: 0;
}
.home__noticias-img {
  width: 22em;
  height: 100%;
  position: relative;
  z-index: -1;
}
.home__center {
  margin: 2em 0 0 0;
}
.home__center-img {
  position: relative;
  background: #00000070;
  height: 40em;
  overflow: hidden;
}
.home__center-img-img {
  object-fit: cover;
}
.home__center-div-div {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  text-align: center;
}
.home__center-span {
  font-size: 5em;
  font-weight: bold;
}
.home__center-p {
  font-weight: bold;
  margin: 0;
  font-size: 6em;
}
.home__end {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column nowrap;
}
.home__end-first {
  width: 60%;
  line-height: 1.2em;
  font-size: 2em;
  text-align: center;
}
.home__end-last {
