  font-size: 1.5em;
  font-weight: bold;
  padding: 0.5em 1.5em;
  border: 0.1em solid #333;
  border-radius: 0.5em;
  cursor: pointer;
  transition: 0.5s ease;
}
.home__end-last:hover {
  border: 0.1em solid #e66a11;
}
